*{

    margin: 0;
    padding: 0;
    background-color: #d2dbdd;
    box-sizing: border-box;
}

.cards{
    width:100%;
    height: auto;
}

img{
    width:210;
    height: 300;
     display: flex;
    justify-content: center;
    align-items: center;
}

.heading_style{
    padding: 30px 0;
    text-align:center ;
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    font-size: 1.5rem;
    margin-bottom: 30px;
}

button{
   padding: 5px 8px;
   text-transform: uppercase;
   font-size: 13px;
   letter-spacing: 2px;
   font-weight: 500;
   color: #886688;
    background-color: #fff;
    outline:none ;
    border:1px solid black;
    cursor: pointer;
}
.card{
    margin: 6%;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0,1);
    background-color: #fff;
    width: 21.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1);
    float: left;
}
.card_info{
    font-size-adjust: 12px;   
    background-color: #fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding: 16px 24px 24px 24px;
    background-color: #fff;
}
.card_img{
    width:100%;
    height: 225px;
    border-top-right-radius: 12px;
    border-top-left-radius: 12px;
}
.card_category{
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    font-family: "Raleway",sans-serif;
}
.card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
    transform: scale(1.05,1.05);
}

@media (max-width:768px){
    .card{
        float: none;
        margin: 5%;
        width: 90%;
    }
}